<template>
  <div>
    <b-form-group label="Select platform">
      <b-form-radio v-model="platform"
        name="platform"
        value=0>Android</b-form-radio>
      <b-form-radio
        v-model="platform"
        name="platform"
        value=1>iOs</b-form-radio>
    </b-form-group>

    <b-form-group label="Select type of task">
      <b-form-radio v-model="selected"
        name="type"
        value=0>Strings to excel</b-form-radio>
      <b-form-radio
        v-model="selected"
        name="type"
        value=1>Excel to strings</b-form-radio>
    </b-form-group>

    <b-form-file
      v-model="file"
      :state="Boolean(file)"
      accept=".xlsx, .xml, .strings"
      placeholder="Choose a file or drop it here..."
      drop-placeholder="Drop file here..."
    ></b-form-file>

    <b-button v-on:click="uploadFile()">Upload file</b-button>
  </div>
</template>

<script>
  export default{
    data(){
      return{
        selected: 0,
        platform: 0,
        file: null
      }
    },
    methods:{
        uploadFile(){
          if(this.file == null){
            alert("Please select file!");
            return;
          }
          this.sendFile();
        },
        sendFile(){
        if(this.file == null){
          return ;
        }

        let formData = new FormData();
        formData.append('upload_file', this.file);
        this.$http.post('https://xmlparser-hedgehog.herokuapp.com/translation/file?taskId='+this.selected + '&platformId='+this.platform,
          formData,
            {
              headers: {
                'Content-Type': 'multipart/form-data'
              },
              responseType: 'arraybuffer'
            })
          .then(result => {
            console.log(result);
            this.forceFileDownload(result);
          }, error => {
            console.log(error);
          })
        },
        forceFileDownload(response){
          var extension = ".xlsx";

          if(this.selected == 1){
            extension = ".zip"
          }
          const url = window.URL.createObjectURL(new Blob([response.data]))
          const link = document.createElement('a')
          link.href = url
          link.setAttribute('download', 'result'+extension) //or any other extension
          document.body.appendChild(link)
          link.click()
        },
      }
    }

</script>
